<template>
  <div class="withdrawal-page">
    <div class="withdrawal-balance">
      <div class="mb-30px">
        <span style="font-size: 20px"
          >可提现余额：&yen;{{ infoData.availableBalance }}</span
        >
        (可提现余额&yen;{{ infoData.availableBalance }} = 余额￥{{
          infoData.balance
        }}
        -
        <span class="blue-text" @click="nonWithdrawableBalanceDrawerRef?.toggle"
          >不可提现余额</span
        >&yen;{{ infoData.ensureBalance }})
      </div>
      <p>1、提现渠道：目前数企仅支持提现到对公账户。</p>
      <p>
        2、提现时效：提现申请提交后，预计在1-3个工作日内审核完成进行打款，具体到账时间以实际到账时间为准。
      </p>
    </div>

    <div class="line"></div>

    <div class="withdraw-immediately">
      <el-button type="primary" @click="withdrawalDialogRef?.toggle"
        >立刻提现
      </el-button>
      <el-tooltip
        class="box-item"
        effect="dark"
        content="只能提现到认证公司的对公账户中。"
        placement="top"
      >
        <el-icon style="color: #9ca6b9; margin-left: 12px; font-size: 20px">
          <QuestionFilled />
        </el-icon>
      </el-tooltip>
    </div>

    <el-tabs
      v-model="activeName"
      class="mt-20px no-border-tabs"
      @tab-change="handleChange"
    >
      <el-tab-pane label="提现中" name="0,4" />
      <el-tab-pane label="提现完成" name="1,2" />
    </el-tabs>

    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 528px)">
        <el-table-column prop="createdTime" label="提现时间" width="200" />
        <el-table-column prop="amount" label="提现金额 ￥" width="140" />
        <el-table-column
          prop="invoicedBalanceOffset"
          label="需冲抵金额 ￥"
          width="160"
        />
        <el-table-column prop="checkStatusText" label="状态" width="130" />
        <el-table-column prop="ownerName" label="提现账户名" min-width="220" />
        <el-table-column prop="account" label="提现账号" width="200" />
        <el-table-column prop="bankName" label="收款银行" width="220" />
        <el-table-column label="发票信息" min-width="120">
          <template #default="{ row }">
            <el-link
              v-if="row.invoice"
              type="primary"
              @click="handleInvoice(row, '发票信息')"
              >查看
            </el-link>
            <el-link
              v-if="row.checkStatus == '4'"
              type="primary"
              @click="handleInvoice(row, '提交发票')"
              >提交发票
            </el-link>
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty />
        </template>
      </el-table>

      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>

  <NonWithdrawableBalanceDrawer
    ref="nonWithdrawableBalanceDrawerRef"
    :ensureBalance="infoData.ensureBalance"
  />

  <WithdrawalDialog
    ref="withdrawalDialogRef"
    :invoiced-amount="infoData.invoicedBalance"
    :account-balance="infoData.balance"
    :refresh="refresh"
  />

  <InvoicingDialog
    ref="invoicingDialogRef"
    :mode="invoiceMode"
    :courier-number="courierNumber"
    :cash-out-no="cashOutNo"
    :refresh="refresh"
  />
</template>
<script setup lang="ts">
import { reactive, ref } from "vue";
import { QuestionFilled } from "@element-plus/icons-vue";
import NonWithdrawableBalanceDrawer from "@views/financial-center/fund-management/withdrawal/NonWithdrawableBalanceDrawer.vue";
import WithdrawalDialog from "@views/financial-center/fund-management/withdrawal/WithdrawalDialog.vue";
import {
  assetsInfoApi,
  cashOutListRecordsApi,
  currentUserDetailApi,
} from "@api/financial-center-api";
import InvoicingDialog from "@views/financial-center/fund-management/withdrawal/InvoicingDialog.vue";
import { TitleMode } from "@views/financial-center/fund-management/withdrawal/types";
import { LocalStorage, usePage } from "jz-tool-lib";
import { FileUrlApi } from "@api/common-api";

const activeName = ref("0,4");
const courierNumber = ref("");
const cashOutNo = ref("");
const agentId = LocalStorage.getItem("bd_agentId");
const nonWithdrawableBalanceDrawerRef = ref();
const withdrawalDialogRef = ref();
const invoicingDialogRef = ref();
const invoiceMode = ref<TitleMode>("发票信息");

const infoData = reactive({
  invoicedBalance: 0, // 已开票金额
  availableBalance: 0, // 可用余额
  balance: 0, // 余额
  ensureBalance: 0, // 保证金
});

const handleChange = (tab, event) => {
  console.log(tab, event);
  resetPageNumber();
  getList();
};

function handleInvoice(row: any, mode: "提交发票" | "发票信息") {
  invoiceMode.value = mode;
  courierNumber.value = row.courierNumber;
  cashOutNo.value = row.cashOutNo;
  if (row.invoice) {
    FileUrlApi.getData(row.invoice).then((res) => {
      console.log(res.data, "line 141");
      invoicingDialogRef.value.setSrcList([res?.data]);
      invoicingDialogRef.value.toggle();
    });
  } else {
    invoicingDialogRef.value.toggle();
  }
}

function currentUserDetail() {
  currentUserDetailApi().then((res) => {
    console.log(res, "line 123");
    localStorage.setItem(
      "bd_currentUserCompanyName",
      res?.data?.companyName || ""
    );
  });
}

function refresh() {
  resetPageNumber();
  getList();
  assetsInfo();
}

const { pageNumberChange, pageSizeChange, pageData, getList, resetPageNumber } =
  usePage({
    listApi: cashOutListRecordsApi,
    props: {
      pageNumber: "current",
      pageSize: "size",
    },
    searchParams() {
      return {
        checkStatus: activeName.value,
        current: pageData.pageNumber,
        size: pageData.pageSize,
      };
    },
    notExecute: true,
  });

function assetsInfo() {
  assetsInfoApi({
    agencyId: agentId,
  }).then((res) => {
    console.log(res, "line 143");
    infoData.invoicedBalance = res?.data?.invoicedBalance || 0;
    infoData.availableBalance = res?.data?.availableBalance || 0;
    infoData.balance = res?.data?.balance || 0;
    infoData.ensureBalance = res?.data?.ensureBalance || 0;
  });
}

currentUserDetail();
getList();
assetsInfo();
</script>
<style scoped lang="scss">
.blue-text {
  color: #0056ff;
  cursor: pointer;
}

.mb-20px {
  margin-bottom: 20px;
}

.mb-30px {
  margin-bottom: 30px;
}

.mt-20px {
  margin-top: 20px;
}

.line {
  margin-top: 30px;
  margin-bottom: 30px;
  height: 1px;
  background: #dcdfe5;
}

.withdrawal-balance {
  padding: 10px;

  p {
    & ~ p {
      margin-top: 6px;
    }
  }
}

.withdraw-immediately {
  display: flex;
  align-items: center;
}
</style>
