<script setup lang="ts">
import { computed, reactive, ref, shallowReadonly, watch } from "vue";
import { copyText, LocalStorage } from "jz-tool-lib";
import { Plus } from "@element-plus/icons-vue";
// import { useClipboard } from "@vueuse/core";
import { ElMessage } from "element-plus";
import { TitleMode } from "@views/financial-center/fund-management/withdrawal/types";
import { burglarproof, serviceName } from "@assets/common";
import {
  cashOutInvoiceApi,
  getCompanyVirtualAccountByAgencyIdApi,
} from "@api/financial-center-api";

const props = defineProps<{
  mode: TitleMode;
  cashOutNo: string; // 交易单号
  courierNumber?: string; // 快递单号
  refresh: () => void; // 刷新列表
  withdrawalAmount?: number | string;
  invoicedAmount?: number;
}>();

const view = computed(() => props.mode === "发票信息");
const remind = computed(() => props.mode === "开票提醒");
const srcList = ref([]);
// const { copy } = useClipboard();
const visible = ref(false);
const formRef = ref();
const uploadRef = ref();
const formData = reactive({
  invoice: null, // 上传发票成功后可以使用 fileId 赋值
  number: "",
});

const { bd_userInfo: userInfo, bd_authorization: authorization } =
  LocalStorage.batchGetItem(["bd_userInfo", "bd_authorization"]);
const burglarproofInfo = burglarproof();
const params = shallowReadonly({
  uploadHeader: {
    Authorization: "Bearer " + authorization,
    xwsse: burglarproofInfo.XWsse,
    timestamp: burglarproofInfo.timestamp,
  },
  uploadData: {
    createUser: userInfo?.profileId,
  },
});
const rechargeInfo = reactive({
  virtualAccount: "",
});
const rules = {
  invoice: [
    {
      required: true,
      message: "请上传发票",
      trigger: "blur",
    },
  ],
  number: [
    {
      required: true,
      message: "请输入发票快递单号",
      trigger: "blur",
    },
  ],
};

watch(visible, (val) => {
  if (!val) {
    props.refresh?.();
    handleReset();
  } else {
    getCompanyVirtualAccountByAgencyId();
  }
});

function handleOk() {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      console.log(valid, "valid");
      submitInvoice();
    }
  });
}

function handleReset() {
  formData.invoice = null;
  formData.number = "";
  srcList.value = [];
  uploadRef.value?.clearFiles();
}

function submitInvoice() {
  const params = {
    cashOutNo: props.cashOutNo,
    invoice: formData.invoice,
    courierNumber: formData.number,
  };
  cashOutInvoiceApi(params).then((res) => {
    console.log(res, "line 73");
    if (res.rspCode === 200) {
      ElMessage.success("提交成功");
      toggle(false);
    } else {
      ElMessage.warning(res.msg || "提交失败请重试");
    }
  });
}

function toggle(val = true) {
  visible.value = val;
}

const handleSuccess = (response, uploadFile) => {
  console.log(response, uploadFile, "line 70");
  if (response.statusCode === 0) {
    formData.invoice = response?.data?.[0];
    formRef.value.validate();
    ElMessage.success("上传成功");
  } else {
    ElMessage.warning(response.msg || "上传失败请重试");
  }
};

function handleRemove() {
  formData.invoice = null;
  formRef.value.validate();
}

const beforeUpload = (rawFile) => {
  console.log(rawFile, 'rawFile');
  // 检查文件类型是否为图片
  if (!rawFile.type.startsWith("image/")) {
    ElMessage.error("只能上传图片文件");
    return false;
  }
  return true;
};

function getCompanyVirtualAccountByAgencyId() {
  getCompanyVirtualAccountByAgencyIdApi().then((res) => {
    console.log(res, "line 141");
    rechargeInfo.virtualAccount = res?.data?.virtualAccount || "";
  });
}

function handleCopy() {
  const text = `账号：${rechargeInfo.virtualAccount}，收款户名：八度云计算（安徽）有限公司，收款银行：中国银行合肥望江中路支行`;
  copyText(text)
    .then(() => {
      ElMessage.success("复制成功");
    })
    .catch(() => {
      ElMessage.success("复制失败");
    });
}

defineExpose({
  toggle,
  setSrcList(data) {
    srcList.value = data;
  },
});
</script>

<template>
  <el-dialog
    v-model="visible"
    :title="props.mode"
    class="custom-dialog"
    :width="600"
  >
    <p v-if="remind" style="margin-bottom: 10px">
      您申请提现的{{ props.withdrawalAmount }}元中，包含了{{
        props.invoicedAmount
      }}元已开票金额。请开具等额增值税 发票给到数企。
      <el-link type="primary" @click="handleCopy">复制开票信息</el-link>
    </p>
    <el-form
      ref="formRef"
      :model="formData"
      :rules="rules"
      :class="view && 'hidden-required'"
    >
      <el-form-item :label="view ? '查看发票：' : '提交发票：'" prop="invoice">
        <template v-if="view">
          <el-image
            style="width: 100px; height: 100px"
            :src="srcList[0]"
            :zoom-rate="1.2"
            :max-scale="7"
            :min-scale="0.2"
            :preview-src-list="srcList"
            :initial-index="4"
            fit="cover"
          />
        </template>
        <div v-else class="box-2">
          <el-upload
            ref="uploadRef"
            class="avatar-uploader"
            :data="params.uploadData"
            :headers="params.uploadHeader"
            name="files"
            accept="image/*"
            action="/base-oss-rest/oss/uploadFile"
            :limit="1"
            :on-success="handleSuccess"
            :before-upload="beforeUpload"
            :on-remove="handleRemove"
          >
            <el-icon class="avatar-uploader-icon"><Plus /></el-icon>
          </el-upload>
          <el-link v-if="!remind" type="primary" @click="handleCopy"
            >复制开票信息</el-link
          >
        </div>
      </el-form-item>
      <el-form-item label="快递单号：" prop="number">
        <template v-if="view">{{ props.courierNumber }}</template>
        <el-input
          v-else
          v-model="formData.number"
          placeholder="请输入发票快递单号"
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div v-if="remind" class="dialog-footer">
        <el-button type="primary" @click="handleOk">立刻提交</el-button>
        <el-button @click="toggle(false)">稍后提交</el-button>
      </div>
      <div v-else class="dialog-footer">
        <el-button v-if="!view" type="primary" @click="handleOk"
          >确定</el-button
        >
        <el-button @click="toggle(false)">关闭</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<style scoped>
.box-2 {
  display: flex;
  align-items: flex-start;
  column-gap: 10px;
  :deep(.el-link__inner) {
    margin-top: 10px;
    line-height: 1;
  }
}
</style>

<style lang="scss">
.avatar-uploader .avatar {
  width: 150px;
  height: 150px;
  display: block;
}

.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 150px;
  height: 150px;
  text-align: center;
}

.hidden-required {
  .el-form-item__label::before,
  .el-form-item__error {
    display: none;
  }
}
</style>
