<script setup lang="ts">
import { ref, watch } from "vue";
import AddWithdrawalAccount from "@views/financial-center/fund-management/withdrawal/AddWithdrawalAccount.vue";
import {
  bankAccountListApi,
  cashOutAddApi,
  getGeneratorNumberApi,
  getInvoicedBalanceApi,
} from "@api/financial-center-api";
import InvoicingDialog from "@views/financial-center/fund-management/withdrawal/InvoicingDialog.vue";
import { QuestionFilled } from "@element-plus/icons-vue";
import { ElMessage } from "element-plus";

const props = defineProps<{
  invoicedAmount: number; // 已开票金额（已废弃，现改为由用户输入后实时调接口返回，现由 _invoicedAmount 替代）
  accountBalance: number; // 账户余额
  refresh: () => void; // 刷新列表
}>();

const visible = ref(false);
const withdrawalAmount = ref("");
const cashOutNo = ref("");
const addWithdrawalAccountRef = ref();
const invoicingDialogRef = ref();
const activeIndex = ref(-1);
const withdrawalAmountList = ref<any[]>([]);
const currentUserCompanyName = ref("");
const _invoicedAmount = ref(0);

watch(visible, (val) => {
  if (val) {
    withdrawalAmount.value = ""; // 需要给提交发票 dialog 提供值，只能在 true 时执行
    _invoicedAmount.value = 0;
    getBankAccountList();
    getGeneratorNumber();
  } else {
    activeIndex.value = -1;
  }
});

function checkAmount() {
  // 校验金额的正则表达式
  const amountRegex = /^(?:0|[1-9]\d*)(?:\.\d{1,3})?$/;

  return (
    amountRegex.test(withdrawalAmount.value) &&
    Number(withdrawalAmount.value) > 0
  );
}

function handleOk() {
  if (activeIndex.value < 0) return ElMessage.warning("请选择提现账户");
  if (!checkAmount()) return ElMessage.warning("请输入正确金额并且金额大于0");
  getGeneratorNumber().then((data) => {
    cashOutNo.value = data;
    cashOutAdd(data);
  });
}

function toggle(val = true) {
  visible.value = val;
}

function handleRefresh() {
  console.log("Refresh withdrawal amount list");
  getBankAccountList();
}

function getBankAccountList() {
  currentUserCompanyName.value =
    localStorage.getItem("bd_currentUserCompanyName") ?? "";
  bankAccountListApi().then((res) => {
    console.log(res, "line 45");
    withdrawalAmountList.value = res?.data || [];
    activeIndex.value = -1;
  });
}

function cashOutAdd(cashOutNo) {
  const { account } = withdrawalAmountList.value[activeIndex.value];
  cashOutAddApi({
    accountBalance: props.accountBalance, // 余额
    account,
    amount: Number(withdrawalAmount.value), // 提现金额
    cashOutNo,
    invoicedBalanceOffset: _invoicedAmount.value, // 增加需冲抵金额字段：invoicedBalanceOffset 即提现中的已开票金额
    type: 2, // 提现方式 0:支付宝 1: 微信 2: 银行卡
  }).then((res) => {
    console.log(res, "line 53");
    visible.value = false;
    ElMessage.success("提交成功！");
    console.log(_invoicedAmount.value, '_invoicedAmount.value');
    if (_invoicedAmount.value > 0) {
      invoicingDialogRef.value.toggle();
    } else {
      // 没有”开票提醒“直接在这里刷新列表
      props.refresh?.();
    }
  });
}

function getGeneratorNumber() {
  return getGeneratorNumberApi().then((res) => {
    return res?.data;
  });
}

function handleChange(amount) {
  getInvoicedBalanceApi({
    amount,
  }).then((res) => {
    console.log(res, "line 109");
    _invoicedAmount.value = Number(res?.data || 0);
  });
}

defineExpose({ toggle });
</script>

<template>
  <el-dialog v-model="visible" title="提现" class="custom-dialog" :width="600">
    <el-form @submit.prevent>
      <el-form-item label="提现金额：">
        <div style="display: flex; align-items: center">
          <el-input
            style="width: 200px; margin-right: 10px"
            v-model="withdrawalAmount"
            placeholder="请输入提现金额"
            @change="handleChange"
          />
          <!--          已开票金额：{{ props.invoicedAmount ?? 0 }}-->
          已开票金额：{{ _invoicedAmount ?? 0 }}
          <el-tooltip
            class="box-item"
            effect="dark"
            content="提交金额中，已开票金额大于0时，需提交等额的增值税发票。"
            placement="top"
          >
            <el-icon style="color: #9ca6b9; margin-left: 12px; font-size: 20px"
              ><QuestionFilled
            /></el-icon>
          </el-tooltip>
        </div>
      </el-form-item>
      <el-form-item label="选择提现账户：" label-position="top">
        <div
          class="account-item"
          v-for="(item, index) of withdrawalAmountList"
          :key="item.account"
          :class="activeIndex === index ? 'active' : ''"
          @click="activeIndex = index"
        >
          <p>账户：{{ item.account }}</p>
          <div class="flex">
            <p>开户名称：{{ currentUserCompanyName }}</p>
            <p>开户银行：{{ item.bankName }}</p>
          </div>
        </div>
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-link
          type="primary"
          style="margin-right: 315px"
          @click="addWithdrawalAccountRef?.toggle"
          >新增提现账户</el-link
        >
        <el-button type="primary" @click="handleOk">确定</el-button>
        <el-button @click="visible = false">关闭</el-button>
      </div>
    </template>
  </el-dialog>

  <AddWithdrawalAccount
    :refresh="handleRefresh"
    ref="addWithdrawalAccountRef"
  />

  <InvoicingDialog
    ref="invoicingDialogRef"
    mode="开票提醒"
    :cash-out-no="cashOutNo"
    :refresh="props.refresh"
    :withdrawal-amount="withdrawalAmount"
    :invoiced-amount="_invoicedAmount"
  />
</template>

<style scoped lang="scss">
.account-item {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  border: 1px solid #dcdfe6;

  &:hover,
  &.active {
    border-color: #0056ff;
  }

  & ~ & {
    margin-top: 10px;
  }
}

.flex {
  display: flex;
  p {
    width: 50%;
  }
}
</style>
