<script setup lang="ts">
import { reactive, ref, watch } from "vue";
import { LocalStorage } from "jz-tool-lib";
import { bankAccountAddApi } from "@api/financial-center-api";

const props = defineProps<{
  refresh: () => void;
}>();

const visible = ref(false);
const currentUserCompanyName = ref("");
const formRef = ref();
const formData = reactive({
  // ownerName: '', 开户名称（没有用到，仅做注释）
  account: "",
  bankName: "",
});

const rules = {
  account: [
    {
      required: true,
      message: "请输入提现账号",
      trigger: "blur",
    },
  ],
  bankName: [
    {
      required: true,
      message: "请输入开户银行",
      trigger: "blur",
    },
  ],
};

watch(visible, (val) => {
  if (val) {
    currentUserCompanyName.value =
      localStorage.getItem("bd_currentUserCompanyName") ?? "";
  } else {
    formRef.value.resetFields();
  }
});

function handleClose() {
  visible.value = false;
}
function handleOk() {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      console.log({
        ...formData,
        ownerName: currentUserCompanyName.value,
      });
      bankAccountAddApi({
        ...formData,
        ownerName: currentUserCompanyName.value,
      }).then(() => {
        props.refresh();
        visible.value = false;
      });
    }
  });
}

function toggle(val = true) {
  visible.value = val;
}

defineExpose({ toggle });
</script>

<template>
  <el-drawer
    :model-value="visible"
    title="新增提现账户"
    size="500"
    class="custom-drawer"
    :before-close="handleClose"
  >
    <el-form ref="formRef" :model="formData" :rules="rules">
      <el-form-item label="开户名称：">
        <div style="padding-left: 10px">{{ currentUserCompanyName }}</div>
      </el-form-item>
      <el-form-item label="提现账号：" prop="account">
        <el-input v-model="formData.account" placeholder="请输入提现账号" />
      </el-form-item>
      <el-form-item label="开户银行：" prop="bankName">
        <el-input v-model="formData.bankName" placeholder="请输入开户银行" />
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="handleOk" type="primary">确定</el-button>
      <el-button @click="toggle(false)">取消</el-button>
    </template>
  </el-drawer>
</template>

<style scoped lang="scss"></style>
