<script setup lang="ts">
import { ref } from "vue";

const props = defineProps<{
  ensureBalance: number;
}>();

const visible = ref(false);

function handleClose() {
  visible.value = false;
}

function toggle(val = true) {
  visible.value = val;
}

defineExpose({ toggle });
</script>

<template>
  <el-drawer
    :model-value="visible"
    title="详情"
    size="600"
    class="custom-drawer"
    :before-close="handleClose"
  >
    <div>
      一、不可提现金额包含哪些组成？
      <br />
      <br />
      不可提现金额 ￥{{ props.ensureBalance }} = 保证金￥{{
        props.ensureBalance
      }}
      <br />
      <br />
      <br />
      二、保证金
      <br />
      <br />
      保证金是为保证代理商业务正常开展，数企扣留下来的返佣金额。
      <br />
      来源如下：
      <br />
      1、购买代理商权服务时会扣留一定比例的保证金金额。
      <br />
      2、代理商客户购买服务，会扣留一定比例的返佣金额作为保证金。
      <br />
      <br />
      保证金在订单消耗后会返还给代理商
      <br />
      例如
      <br />
      1、代理权产品服务到期后，保证金一次性返还给代理商；
      <br />
      2、呼叫套餐订单返佣的保证金，按月返还已使用部分的保证金；
      <br />
      3、话费合作，按客户实际消耗返还保证金；
    </div>
    <template #footer>
      <el-button @click="toggle(false)">关闭</el-button>
    </template>
  </el-drawer>
</template>

<style scoped lang="scss"></style>
